import React from "react";
import Faded from "../../effect/Faded";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import ArchiveBox from './ArchiveBox';


function Archive() {
    const NextArrow = ({ onClick }) => {
        return (
            <button id="slider-next"
                onClick={onClick}
                type='button'
            >
            </button>
        );
    };
    
    const PrevArrow = ({ onClick }) => {
        return (
            <button id="slider-prev"
                onClick={onClick}
                type='button'
            >
            </button>
        );
    };
    const settings = {
          dots: true,
          infinite: true,
          speed: 500,
          slidesToShow: 1,
          slidesToScroll: 1,
          nextArrow: <NextArrow />,
          prevArrow: <PrevArrow />,
          appendDots: (dots) => (
            <div
              style={{
                width: '100%',
                position: 'absolute',
                top: '550px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}
            >
              <ul> {dots} </ul>
            </div>
          ),
          dotsClass: 'dots_custom'
        };
    return (
      <Faded className='archive-wrap'>
          <div className='archive-container'>
              <div className='archive-title'>Archive</div>
              <Slider {...settings}>
                <ArchiveBox date={'2024-12-13'} text={<div><p> Archive .     .<br/>
                           .<br/>
                          .<br/>
                       .<br/>
                       .<br/>

                    </p><p style={{fontFamily:'Prompt', fontSize:'15px'}}><br/>
                                                                                       <br/>
                                                                                        <br/>
                                                                                       <br/>
                                                                                       <br/>
                                                                                        (*)<br/>
                                                                                       (__//_<br/>
                                                                                       //<br/></p></div>}/>
                <ArchiveBox date={'2024-12-16'} text={(<p> .     .<br/>
                      react-slick  Archive  .<br/>
                        MBTI  .<br/>
                          .<br/>
                             .<br/>
                      ,        .<br/>
                        . . .</p>)}/>
                <ArchiveBox date={'2024-12-17'} text={(<p>     .<br/>
                        DB   .<br/>
                           .<br/>
                           .<br/>
                           .<br/>
                       .</p>)}/>
                <ArchiveBox date={'2024-12-18'} text={(<p>      .<br/>
                             .<br/>
                      State     reload  .<br/>
                        .<br/></p>)}/>
                <ArchiveBox date={'2024-12-24'} text={(<p> ~<br/>
                          ..<br/>
                       ?
                    </p>)}/>
                <ArchiveBox date={'2025-01-17'} text={(<p> .<br/>
                     .<br/>
                      .<br/>
                     .
                </p>)}/>
                <ArchiveBox date={'2025-05-08'} text={(<p>..<br/>
                5      .. <br/>
                 !<br/>
                  
                </p>)}/>
              </Slider>
          </div>
      </Faded>
    );
}

export default Archive;
